
"use client"
import { Edit2, Trash2, ChevronLeft, ChevronRight } from "lucide-react";
import { Key, ReactElement, JSXElementConstructor, ReactNode, ReactPortal } from "react";
 
interface listprops{
currentStudents :any, 
  handleDelete :any, 
  currentPage :any, 
  setCurrentPage :any, 
  totalPages :any, 
  totalCount :any,
  startIndex :any,
  endIndex:any 
}
export function List({ 
  currentStudents, 
  handleDelete, 
  currentPage, 
  setCurrentPage, 
  totalPages, 
  totalCount,
  startIndex,
  endIndex 
  
}:listprops) {
  return (
    <div className="overflow-x-auto">
      <table className="w-full text-left border-collapse">
        <thead>
          <tr className="bg-gray-100 text-gray-600 uppercase text-sm">
            <th className="py-3 px-6">ID</th>
            <th className="py-3 px-6">Name</th>
            <th className="py-3 px-6 text-center">Sex</th>
            <th className="py-3 px-6 text-center">Actions</th>
          </tr>
        </thead>
        <tbody className="text-gray-600 text-sm font-light">
          {currentStudents.map((student: { student_id: Key | null | undefined; admission_no: string | number | bigint | boolean | ReactElement<unknown, string | JSXElementConstructor<any>> | Iterable<ReactNode> | ReactPortal | Promise<string | number | bigint | boolean | ReactPortal | ReactElement<unknown, string | JSXElementConstructor<any>> | Iterable<ReactNode> | null | undefined> | null | undefined; name: string | number | bigint | boolean | ReactElement<unknown, string | JSXElementConstructor<any>> | Iterable<ReactNode> | ReactPortal | Promise<string | number | bigint | boolean | ReactPortal | ReactElement<unknown, string | JSXElementConstructor<any>> | Iterable<ReactNode> | null | undefined> | null | undefined; sex: string | number | bigint | boolean | ReactElement<unknown, string | JSXElementConstructor<any>> | Iterable<ReactNode> | Promise<string | number | bigint | boolean | ReactPortal | ReactElement<unknown, string | JSXElementConstructor<any>> | Iterable<ReactNode> | null | undefined> | null | undefined; }) => (
            <tr key={student.student_id} className="border-b hover:bg-gray-50">
              <td className="py-3 px-6">{student.admission_no}</td>
              <td className="py-3 px-6 font-medium">{student.name}</td>
              <td className="py-3 px-6 text-center">
                <span className={`py-1 px-3 rounded-full text-xs ${student.sex === 'Male' ? 'bg-blue-100 text-blue-600' : 'bg-pink-100 text-pink-600'}`}>
                  {student.sex}
                </span>
              </td>
              <td className="py-3 px-6 text-center">
                <div className="flex justify-center gap-3">
                  <button className="hover:text-blue-500"><Edit2 size={18} /></button>
                  <button onClick={() => handleDelete(student.student_id)} className="hover:text-red-500"><Trash2 size={18} /></button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/* Pagination Inside List */}
      <div className="p-4 border-t flex justify-between items-center bg-gray-50">
        <span className="text-sm text-gray-600">
          Showing {startIndex + 1} to {Math.min(endIndex, totalCount)} of {totalCount}
        </span>
        <div className="flex gap-2">
          <button 
            disabled={currentPage === 1}
            onClick={() => setCurrentPage(currentPage - 1)}
            className="p-2 border rounded disabled:opacity-50"
          ><ChevronLeft size={16} /></button>
          <button 
            disabled={currentPage === totalPages}
            onClick={() => setCurrentPage(currentPage + 1)}
            className="p-2 border rounded disabled:opacity-50"
          ><ChevronRight size={16} /></button>
        </div>
      </div>
    </div>
  );
}